// import React from "react";
import { configureStore, createSlice } from '@reduxjs/toolkit';

const initialData = [
  {
    id: 1,
    text: ' ',
    done: true
  },
  {
    id: 2,
    text: ' ',
    done: true
  },
  {
    id: 3,
    text: 'Context ',
    done: false
  },
  {
    id: 4,
    text: ' ',
    done: false
  }
];

const todoSlice = createSlice({
  name: 'todo',
  initialState: initialData,
  reducers: {
    toggle: (state, action) => {
      return state.map(data => data.id === action.payload ? {...data, done:!data.done}: data)
    },
    remove: (state, action) => {
      return state.filter(data => data.id === action.payload ? null : data)
    },
    make: (state, action) => {
      state.push(action.payload)
    }
  }
});

export const { toggle, remove, make } = todoSlice.actions;

const todoMaxId = createSlice({
  name: 'todoMaxId',
  initialState: [...initialData].sort(function(a, b){ return b.id - a.id })[0].id,
  reducers: {
    nextId: (state) => {
      return (state + 1)
    }
  }
})

export const { nextId } = todoMaxId.actions;

export default configureStore({
  reducer: {
    todo: todoSlice.reducer,
    todoMaxId : todoMaxId.reducer
  }
})