import React, { useEffect, useState } from "react";
import axios from "axios";
import { Navbar, Container, Nav, Form, FormControl, Button } from "react-bootstrap";
import { useNavigate, useParams } from "react-router-dom";

function NavList() {
  const [axiosData, setAxiosData] = useState([]);
  useEffect(() => {
    axios.get('/data.json')
    .then(res => setAxiosData([...res.data.category1]))
    // .then(res => console.log(res.data.category1))
    .catch(err => console.log(err));
  }, [])

  const navigate = useNavigate();
  const { id } = useParams();

  return (
    <>
    <Navbar bg="light" expand="lg">
      <Container fluid>
        <Navbar.Brand style={{'cursor':'pointer'}} onClick={() => { navigate('/') }}>ToDoList</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
            >
            {
              axiosData.map(x => <Nav.Link onClick={() => { navigate(x.link) }} key={x.id}>{x.title}</Nav.Link>)
            }
            {/* <Nav.Link href="#action1">Home</Nav.Link>
            <Nav.Link href="#action2">Link</Nav.Link>
            <NavDropdown title="Link" id="navbarScrollingDropdown">
            <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
            <NavDropdown.Item href="#action4">Another action</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item href="#action5">
            Something else here
            </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link href="#" disabled>
            Link
          </Nav.Link> */}
          </Nav>
          <Form className="d-flex">
            <FormControl
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-success">Search</Button>
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    {
      id && <div>{id}</div>
    }
    </>
  )
}

export default NavList;